/**
 * PRINTs
 * Fix only important bugs for media="print"
 * theme: twenty20-vignoble
 */
body {
    position: relative;

    #site-header,
    #site-footer {
        display: none;
        position: absolute;
        left: -99999in;
        opacity: 0;
    }
    #site-content {
        .section-inner {
            margin-left: 0;
            margin-right: 0;
        }
    }
}
